.navbar {
  width: 100%;
  height: 60px;
  line-height: 60px;
  position: fixed;
  top: 0;
  left: 0;
  font-size: 14px;
  background-color: rgba(0, 0, 0, 0.1);
  -webkit-transition: background-color .3s;
  -o-transition: background-color .3s;
  transition: background-color .3s;
  z-index: 1000;
}
.navbar .nav_list_min {
    display: none;
    position: absolute;
    top: 0;
    right: 0;
    color: #fff;
}
.navbar .nav_list_min li {
      display: block;
}
.navbar .nav_list_min .nav_title {
      text-align: center;
      height: 60px;
      width: 60px;
}
.navbar .nav_list_min .nav_title i {
        font-size: 32px;
}
.navbar .nav_list_min_box {
    position: absolute;
    right: 0;
    width: 100%;
    background: #1a1818;
    background: -webkit-gradient(linear, left top, right top, color-stop(35%, #1a1818), to(#353030));
    background: -webkit-linear-gradient(left, #1a1818 35%, #353030 100%);
    background: -o-linear-gradient(left, #1a1818 35%, #353030 100%);
    background: linear-gradient(to right, #1a1818 35%, #353030 100%);
    font-size: 0.4rem;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    padding: 0.2rem;
    text-align: center;
    color: #fff;
    border-top: 1px solid rgba(255, 255, 255, 0.1);
    -webkit-transform-origin: top;
        -ms-transform-origin: top;
            transform-origin: top;
    -webkit-transform: translateY(-30%) scaleY(0.8);
        -ms-transform: translateY(-30%) scaleY(0.8);
            transform: translateY(-30%) scaleY(0.8);
    opacity: 0;
    z-index: -1;
    -webkit-transition: opacity .3s,-webkit-transform .3s;
    transition: opacity .3s,-webkit-transform .3s;
    -o-transition: transform .3s,opacity .3s;
    transition: transform .3s,opacity .3s;
    transition: transform .3s,opacity .3s,-webkit-transform .3s;
    visibility: hidden;
}
.navbar .nav_list_min_box li {
      height: 40px;
      line-height: 40px;
}
.navbar .open {
    visibility: unset;
    -webkit-transform: translateY(0) scaleY(1);
        -ms-transform: translateY(0) scaleY(1);
            transform: translateY(0) scaleY(1);
    opacity: 1;
    z-index: 2;
}
.navbar .logo {
    height: 60px;
    line-height: 60px;
    padding: 6px 0;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
.navbar .logo img {
      height: 48px;
}
.navbar .page_container {
    position: relative;
}
.navbar .nav_list {
    position: absolute;
    top: 0;
    right: 0;
}
.navbar .nav_list .nav_item {
    display: inline-block;
    color: #fff;
}
.navbar .nav_list .nav_item a {
      padding: 0 15px;
      -webkit-transition: color .3s;
      -o-transition: color .3s;
      transition: color .3s;
      height: 60px;
      line-height: 60px;
      display: inline-block;
}
.navbar .nav_list .nav_item a:hover {
        color: #62be93;
}
.navbar .router-link-exact-active {
    color: #62be93;
}
@media screen and (max-width: 736px) {
.navbar .nav_list {
      display: none;
}
.navbar .nav_list_min {
      display: block;
}
.navbar .logo {
      padding-left: 20px;
}
}
