* {
  padding: 0;
  margin: 0;
}
a {
  color: inherit;
  text-decoration: none;
}
body {
  font-size: 0.4rem;
  font-family: '-apple-system,SF UI Text,Arial,PingFang SC,Hiragino Sans GB,Microsoft YaHei,WenQuanYi Micro Hei,sans-serif';
}
.view {
  min-height: 100vh;
}
#app {
  overflow: hidden;
}
#app .page_container {
    width: 100%;
    margin: 0 auto;
}
@media screen and (min-width: 736px) {
