      width: 1250px;
}
#app .article_item {
      width: 33%;
}
#app .prod_item {
      width: 25%;
}
}
#app .bg_img {
    width: 100%;
    background-image: url("/static/img/home_bg.jpg");
    background-position: center;
    height: 350px;
